___CSS_LOADER_EXPORT___.push([module.id, ".club-form[data-v-26dcaf3a]{margin:0 auto;max-width:800px}.form-group label[data-v-26dcaf3a]{color:#495057;font-weight:600}.invalid-feedback[data-v-26dcaf3a]{display:block}.btn-group .btn[data-v-26dcaf3a]{border-radius:.25rem}.btn-group .btn[data-v-26dcaf3a]:not(:last-child){margin-right:.25rem}", "",{"version":3,"sources":["webpack://./resources/js/components/forms/ClubForm.vue"],"names":[],"mappings":"AA8YA,4BAEA,aAAA,CADA,eAEA,CAEA,mCAEA,aAAA,CADA,eAEA,CAEA,mCACA,aACA,CAEA,iCACA,oBACA,CAEA,kDACA,mBACA","sourcesContent":["<template>\r\n    <div class=\"club-form\">\r\n        <div class=\"card\">\r\n            <div class=\"card-header\">\r\n                <h3 class=\"card-title\">\r\n                    <i class=\"fas fa-users mr-2\"></i>\r\n                    {{ isEditing ? 'Editar Club' : 'Crear Nuevo Club' }}\r\n                </h3>\r\n                <div class=\"card-tools\">\r\n                    <router-link :to=\"{ name: 'clubs' }\" class=\"btn btn-outline-secondary btn-sm\">\r\n                        <i class=\"fas fa-arrow-left mr-1\"></i>\r\n                        Volver a Clubes\r\n                    </router-link>\r\n                </div>\r\n            </div>\r\n\r\n            <div class=\"card-body\">\r\n                <form @submit.prevent=\"submitForm\">\r\n                    <div class=\"row\">\r\n                        <!-- Información básica -->\r\n                        <div class=\"col-md-8\">\r\n                            <div class=\"form-group\">\r\n                                <label for=\"name\">Nombre del Club *</label>\r\n                                <input \r\n                                    id=\"name\"\r\n                                    v-model=\"form.name\" \r\n                                    type=\"text\" \r\n                                    class=\"form-control\"\r\n                                    :class=\"{ 'is-invalid': errors.name }\"\r\n                                    required\r\n                                >\r\n                                <div v-if=\"errors.name\" class=\"invalid-feedback\">\r\n                                    {{ errors.name[0] }}\r\n                                </div>\r\n                            </div>\r\n\r\n                            <div class=\"form-group\">\r\n                                <label for=\"description\">Descripción</label>\r\n                                <textarea \r\n                                    id=\"description\"\r\n                                    v-model=\"form.description\"\r\n                                    class=\"form-control\"\r\n                                    rows=\"3\"\r\n                                    :class=\"{ 'is-invalid': errors.description }\"\r\n                                ></textarea>\r\n                                <div v-if=\"errors.description\" class=\"invalid-feedback\">\r\n                                    {{ errors.description[0] }}\r\n                                </div>\r\n                            </div>\r\n\r\n                            <div class=\"row\">\r\n                                <div class=\"col-md-6\">\r\n                                    <div class=\"form-group\">\r\n                                        <label for=\"city\">Ciudad</label>\r\n                                        <input \r\n                                            id=\"city\"\r\n                                            v-model=\"form.city\" \r\n                                            type=\"text\" \r\n                                            class=\"form-control\"\r\n                                            :class=\"{ 'is-invalid': errors.city }\"\r\n                                        >\r\n                                        <div v-if=\"errors.city\" class=\"invalid-feedback\">\r\n                                            {{ errors.city[0] }}\r\n                                        </div>\r\n                                    </div>\r\n                                </div>\r\n                                <div class=\"col-md-6\">\r\n                                    <div class=\"form-group\">\r\n                                        <label for=\"state\">Estado/Provincia</label>\r\n                                        <input \r\n                                            id=\"state\"\r\n                                            v-model=\"form.state\" \r\n                                            type=\"text\" \r\n                                            class=\"form-control\"\r\n                                            :class=\"{ 'is-invalid': errors.state }\"\r\n                                        >\r\n                                        <div v-if=\"errors.state\" class=\"invalid-feedback\">\r\n                                            {{ errors.state[0] }}\r\n                                        </div>\r\n                                    </div>\r\n                                </div>\r\n                            </div>\r\n\r\n                            <div class=\"row\">\r\n                                <div class=\"col-md-6\">\r\n                                    <div class=\"form-group\">\r\n                                        <label for=\"country\">País</label>\r\n                                        <input \r\n                                            id=\"country\"\r\n                                            v-model=\"form.country\" \r\n                                            type=\"text\" \r\n                                            class=\"form-control\"\r\n                                            :class=\"{ 'is-invalid': errors.country }\"\r\n                                        >\r\n                                        <div v-if=\"errors.country\" class=\"invalid-feedback\">\r\n                                            {{ errors.country[0] }}\r\n                                        </div>\r\n                                    </div>\r\n                                </div>\r\n                                <div class=\"col-md-6\">\r\n                                    <div class=\"form-group\">\r\n                                        <label for=\"founded_year\">Año de Fundación</label>\r\n                                        <input \r\n                                            id=\"founded_year\"\r\n                                            v-model=\"form.founded_year\" \r\n                                            type=\"number\" \r\n                                            class=\"form-control\"\r\n                                            :class=\"{ 'is-invalid': errors.founded_year }\"\r\n                                            min=\"1900\"\r\n                                            :max=\"currentYear\"\r\n                                        >\r\n                                        <div v-if=\"errors.founded_year\" class=\"invalid-feedback\">\r\n                                            {{ errors.founded_year[0] }}\r\n                                        </div>\r\n                                    </div>\r\n                                </div>\r\n                            </div>\r\n\r\n                            <div class=\"form-group\">\r\n                                <label for=\"website\">Sitio Web</label>\r\n                                <input \r\n                                    id=\"website\"\r\n                                    v-model=\"form.website\" \r\n                                    type=\"url\" \r\n                                    class=\"form-control\"\r\n                                    :class=\"{ 'is-invalid': errors.website }\"\r\n                                    placeholder=\"https://ejemplo.com\"\r\n                                >\r\n                                <div v-if=\"errors.website\" class=\"invalid-feedback\">\r\n                                    {{ errors.website[0] }}\r\n                                </div>\r\n                            </div>\r\n\r\n                            <div class=\"form-group\">\r\n                                <label for=\"status\">Estado</label>\r\n                                <select \r\n                                    id=\"status\"\r\n                                    v-model=\"form.status\" \r\n                                    class=\"form-control\"\r\n                                    :class=\"{ 'is-invalid': errors.status }\"\r\n                                >\r\n                                    <option value=\"active\">Activo</option>\r\n                                    <option value=\"inactive\">Inactivo</option>\r\n                                    <option value=\"suspended\">Suspendido</option>\r\n                                </select>\r\n                                <div v-if=\"errors.status\" class=\"invalid-feedback\">\r\n                                    {{ errors.status[0] }}\r\n                                </div>\r\n                            </div>\r\n                        </div>\r\n\r\n                        <!-- Logo -->\r\n                        <div class=\"col-md-4\">\r\n                            <div class=\"form-group\">\r\n                                <label>Logo del Club</label>\r\n                                <div class=\"text-center\">\r\n                                    <div v-if=\"logoPreview\" class=\"mb-3\">\r\n                                        <img \r\n                                            :src=\"logoPreview\" \r\n                                            alt=\"Logo preview\" \r\n                                            class=\"img-fluid rounded\"\r\n                                            style=\"max-width: 200px; max-height: 200px; object-fit: cover;\"\r\n                                        >\r\n                                    </div>\r\n                                    <div v-else-if=\"form.logo\" class=\"mb-3\">\r\n                                        <img \r\n                                            :src=\"'/storage/' + form.logo\" \r\n                                            alt=\"Logo actual\" \r\n                                            class=\"img-fluid rounded\"\r\n                                            style=\"max-width: 200px; max-height: 200px; object-fit: cover;\"\r\n                                        >\r\n                                    </div>\r\n                                    <div v-else class=\"mb-3\">\r\n                                        <div class=\"bg-light border rounded d-flex align-items-center justify-content-center\" style=\"height: 150px;\">\r\n                                            <i class=\"fas fa-image fa-3x text-muted\"></i>\r\n                                        </div>\r\n                                    </div>\r\n                                    \r\n                                    <input \r\n                                        ref=\"logoInput\"\r\n                                        type=\"file\" \r\n                                        accept=\"image/*\" \r\n                                        @change=\"handleLogoChange\"\r\n                                        class=\"d-none\"\r\n                                    >\r\n                                    <button \r\n                                        type=\"button\" \r\n                                        @click=\"$refs.logoInput.click()\" \r\n                                        class=\"btn btn-outline-primary btn-sm\"\r\n                                    >\r\n                                        <i class=\"fas fa-upload mr-1\"></i>\r\n                                        Subir Logo\r\n                                    </button>\r\n                                    <button \r\n                                        v-if=\"logoPreview || form.logo\" \r\n                                        type=\"button\" \r\n                                        @click=\"removeLogo\" \r\n                                        class=\"btn btn-outline-danger btn-sm ml-2\"\r\n                                    >\r\n                                        <i class=\"fas fa-trash mr-1\"></i>\r\n                                        Quitar\r\n                                    </button>\r\n                                </div>\r\n                                <div v-if=\"errors.logo\" class=\"invalid-feedback d-block\">\r\n                                    {{ errors.logo[0] }}\r\n                                </div>\r\n                                <small class=\"form-text text-muted\">\r\n                                    Formatos: JPG, PNG, GIF. Tamaño máximo: 2MB\r\n                                </small>\r\n                            </div>\r\n                        </div>\r\n                    </div>\r\n                </form>\r\n            </div>\r\n\r\n            <div class=\"card-footer\">\r\n                <div class=\"d-flex justify-content-between\">\r\n                    <router-link :to=\"{ name: 'clubs' }\" class=\"btn btn-secondary\">\r\n                        <i class=\"fas fa-times mr-1\"></i>\r\n                        Cancelar\r\n                    </router-link>\r\n                    <button \r\n                        type=\"submit\" \r\n                        @click=\"submitForm\"\r\n                        :disabled=\"loading\" \r\n                        class=\"btn btn-primary\"\r\n                    >\r\n                        <i v-if=\"loading\" class=\"fas fa-spinner fa-spin mr-1\"></i>\r\n                        <i v-else class=\"fas fa-save mr-1\"></i>\r\n                        {{ isEditing ? 'Actualizar Club' : 'Crear Club' }}\r\n                    </button>\r\n                </div>\r\n            </div>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n    name: 'ClubForm',\r\n    data() {\r\n        return {\r\n            loading: false,\r\n            logoFile: null,\r\n            logoPreview: null,\r\n            form: {\r\n                name: '',\r\n                description: '',\r\n                city: '',\r\n                state: '',\r\n                country: '',\r\n                founded_year: null,\r\n                website: '',\r\n                status: 'active',\r\n                logo: null\r\n            },\r\n            errors: {}\r\n        };\r\n    },\r\n    computed: {\r\n        isEditing() {\r\n            return !!this.$route.params.id;\r\n        },\r\n        clubId() {\r\n            return this.$route.params.id;\r\n        },\r\n        currentYear() {\r\n            return new Date().getFullYear();\r\n        }\r\n    },\r\n    mounted() {\r\n        if (this.isEditing) {\r\n            this.loadClub();\r\n        }\r\n    },\r\n    methods: {\r\n        async loadClub() {\r\n            this.loading = true;\r\n            try {\r\n                const response = await fetch(`/api/clubs/${this.clubId}`);\r\n                if (!response.ok) {\r\n                    throw new Error('Error al cargar el club');\r\n                }\r\n                const data = await response.json();\r\n                this.form = { ...this.form, ...data };\r\n            } catch (error) {\r\n                console.error('Error loading club:', error);\r\n                this.showNotification('Error al cargar el club', 'error');\r\n                this.$router.push({ name: 'clubs' });\r\n            } finally {\r\n                this.loading = false;\r\n            }\r\n        },\r\n\r\n        handleLogoChange(event) {\r\n            const file = event.target.files[0];\r\n            if (!file) return;\r\n\r\n            // Validar tipo de archivo\r\n            if (!file.type.startsWith('image/')) {\r\n                this.showNotification('Por favor selecciona un archivo de imagen válido', 'error');\r\n                return;\r\n            }\r\n\r\n            // Validar tamaño (2MB)\r\n            if (file.size > 2 * 1024 * 1024) {\r\n                this.showNotification('El archivo debe ser menor a 2MB', 'error');\r\n                return;\r\n            }\r\n\r\n            this.logoFile = file;\r\n\r\n            // Crear preview\r\n            const reader = new FileReader();\r\n            reader.onload = (e) => {\r\n                this.logoPreview = e.target.result;\r\n            };\r\n            reader.readAsDataURL(file);\r\n        },\r\n\r\n        removeLogo() {\r\n            this.logoFile = null;\r\n            this.logoPreview = null;\r\n            this.form.logo = null;\r\n            if (this.$refs.logoInput) {\r\n                this.$refs.logoInput.value = '';\r\n            }\r\n        },\r\n\r\n        async submitForm() {\r\n            this.loading = true;\r\n            this.errors = {};\r\n\r\n            try {\r\n                const formData = new FormData();\r\n                \r\n                // Agregar campos del formulario\r\n                Object.keys(this.form).forEach(key => {\r\n                    if (key !== 'logo' && this.form[key] !== null && this.form[key] !== '') {\r\n                        formData.append(key, this.form[key]);\r\n                    }\r\n                });\r\n\r\n                // Agregar logo si hay uno nuevo\r\n                if (this.logoFile) {\r\n                    formData.append('logo', this.logoFile);\r\n                }\r\n\r\n                const url = this.isEditing ? `/api/clubs/${this.clubId}` : '/api/clubs';\r\n                const method = this.isEditing ? 'POST' : 'POST';\r\n                \r\n                if (this.isEditing) {\r\n                    formData.append('_method', 'PUT');\r\n                }\r\n\r\n                const response = await fetch(url, {\r\n                    method: method,\r\n                    headers: {\r\n                        'X-CSRF-TOKEN': document.querySelector('meta[name=\"csrf-token\"]').getAttribute('content'),\r\n                    },\r\n                    body: formData\r\n                });\r\n\r\n                const data = await response.json();\r\n\r\n                if (!response.ok) {\r\n                    if (data.errors) {\r\n                        this.errors = data.errors;\r\n                    }\r\n                    throw new Error(data.message || 'Error al guardar el club');\r\n                }\r\n\r\n                this.showNotification(\r\n                    this.isEditing ? 'Club actualizado exitosamente' : 'Club creado exitosamente',\r\n                    'success'\r\n                );\r\n                \r\n                this.$router.push({ name: 'clubs' });\r\n\r\n            } catch (error) {\r\n                console.error('Error saving club:', error);\r\n                this.showNotification(error.message || 'Error al guardar el club', 'error');\r\n            } finally {\r\n                this.loading = false;\r\n            }\r\n        },\r\n\r\n        showNotification(message, type = 'info') {\r\n            console.log(`${type.toUpperCase()}: ${message}`);\r\n            if (type === 'error') {\r\n                alert('Error: ' + message);\r\n            }\r\n        }\r\n    }\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.club-form {\r\n    max-width: 800px;\r\n    margin: 0 auto;\r\n}\r\n\r\n.form-group label {\r\n    font-weight: 600;\r\n    color: #495057;\r\n}\r\n\r\n.invalid-feedback {\r\n    display: block;\r\n}\r\n\r\n.btn-group .btn {\r\n    border-radius: 0.25rem;\r\n}\r\n\r\n.btn-group .btn:not(:last-child) {\r\n    margin-right: 0.25rem;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
